import React ,{ Component } from 'react';
import VideoStript from './videoStript';



class Stript extends Component{
	state ={
		scrollTop:0
	};

	componentDidMount(){
        window.addEventListener('scroll', () => this.listenEvent(this.state));  
    };

   listenEvent(state) {
    const position = window.pageYOffset;
    this.setState({scrollTop:position});
   };

	format(){
		return this.state.scrollTop <= 210 ? "stript2" : "stript1";
	};
	render(){
		return(
			<div className={this.format()}>
				<VideoStript height={this.state.scrollTop}/>
				<h1 style={{marginLeft:200,marginTop:150,color:'white',position:"absolute",fontSize:150}}><strong>Indestry &amp; Energy</strong></h1>
			</div>
			);
	};
}

export default Stript;